  -ms-text-size-adjust: 100%;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
}

body,
button,
input,
select,
textarea {
  color: #222223;
  font-size: 19px;
  font-size: 1.9rem;
  font-weight: 400;
  line-height: 29px;
